<script>
import { Icon } from "@iconify/vue2";
import ingredientData from "@/assets/json/ingredient.json";

export default {
  name: 'HomeView',
  components: {
    Icon
  },
  data() {
    return {
      jenisPelanggaran: ingredientData.jenis_pelanggaran
    }
  },
  methods: {
    getIconForPelanggaran(title) {
      const iconMap = {
        'Suap': 'mdi:cash-multiple',
        'Penipuan': 'mdi:account-alert',
        'Penggelapan': 'mdi:safe',
        'Pencurian': 'mdi:lock-open-variant',
        'Pemerasan': 'mdi:alert-octagon',
        'Korupsi': 'mdi:cash-remove',
        'Kode Etik': 'mdi:book-open-variant',
        'Kecurangan': 'mdi:account-remove',
        'Gratifikasi': 'mdi:gift',
        'Benturan Kepentingan': 'mdi:arrow-decision'
      };
      return iconMap[title] || 'mdi:alert-circle';
    },
    
    getColorForPelanggaran(index) {
      const colors = [
        'text-red-500',
        'text-yellow-500', 
        'text-purple-500',
        'text-blue-500',
        'text-green-500',
        'text-pink-500',
        'text-indigo-500',
        'text-yellow-500',
        'text-green-300',
        'text-gray-500'
      ];
      return colors[index % colors.length];
    },

    // New methods for enhanced styling
    getBgColorForPelanggaran(index) {
      const colors = [
        'bg-gradient-to-br from-red-500 to-red-600',
        'bg-gradient-to-br from-yellow-500 to-yellow-600', 
        'bg-gradient-to-br from-purple-500 to-purple-600',
        'bg-gradient-to-br from-blue-500 to-blue-600',
        'bg-gradient-to-br from-green-500 to-green-600',
        'bg-gradient-to-br from-pink-500 to-pink-600',
        'bg-gradient-to-br from-indigo-500 to-indigo-600',
        'bg-gradient-to-br from-yellow-500 to-yellow-600',
        'bg-gradient-to-br from-green-300 to-green-400',
        'bg-gradient-to-br from-gray-500 to-gray-600'
      ];
      return colors[index % colors.length];
    },

    getTextColorForPelanggaran(index) {
      const colors = [
        'text-red-700',
        'text-yellow-700', 
        'text-purple-700',
        'text-blue-700',
        'text-green-700',
        'text-pink-700',
        'text-indigo-700',
        'text-yellow-700',
        'text-green-500',
        'text-gray-700'
      ];
      return colors[index % colors.length];
    },

    getLightBgColorForPelanggaran(index) {
      const colors = [
        'bg-red-100',
        'bg-yellow-100', 
        'bg-purple-100',
        'bg-blue-100',
        'bg-green-100',
        'bg-pink-100',
        'bg-indigo-100',
        'bg-yellow-100',
        'bg-green-100',
        'bg-gray-100'
      ];
      return colors[index % colors.length];
    },

    getGlowColorForPelanggaran(index) {
      const colors = [
        'bg-gradient-to-r from-red-400/20 to-red-600/20',
        'bg-gradient-to-r from-orange-400/20 to-orange-600/20', 
        'bg-gradient-to-r from-purple-400/20 to-purple-600/20',
        'bg-gradient-to-r from-blue-400/20 to-blue-600/20',
        'bg-gradient-to-r from-green-400/20 to-green-600/20',
        'bg-gradient-to-r from-pink-400/20 to-pink-600/20',
        'bg-gradient-to-r from-indigo-400/20 to-indigo-600/20',
        'bg-gradient-to-r from-yellow-400/20 to-yellow-600/20',
        'bg-gradient-to-r from-teal-400/20 to-teal-600/20',
        'bg-gradient-to-r from-gray-400/20 to-gray-600/20'
      ];
      return colors[index % colors.length];
    },
    
    scrollToReport() {
      const reportSection = document.getElementById('report');
      if (reportSection) {
        const navHeight = 80; // Tinggi navigation bar
        const targetPosition = reportSection.offsetTop - navHeight;
        
        window.scrollTo({
          top: targetPosition,
          behavior: 'smooth'
        });
      }
    },
    scrollToAbout() {
      const aboutSection = document.getElementById('about');
      if (aboutSection) {
        const navHeight = 80; // Tinggi navigation bar
        const targetPosition = aboutSection.offsetTop - navHeight;
        
        window.scrollTo({
          top: targetPosition,
          behavior: 'smooth'
        });
      }
    },
    redirectToReport() {
      // Redirect ke halaman laporan atau form laporan terpisah
      this.$message.info('Fitur pembuatan laporan akan segera tersedia');
      // Bisa diganti dengan: this.$router.push('/report-form');
    }
  }
}
