    linear-gradient(90deg, rgba(59, 130, 246, 0.03) 1px, transparent 1px);
  background-size: 50px 50px;
  animation: gridMove 30s linear infinite;
  z-index: 1;
}

/* Radial Gradient Overlay */
.hero-radial-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    ellipse 80% 50% at 50% 50%,
    rgba(16, 185, 129, 0.05) 0%,
    rgba(59, 130, 246, 0.05) 30%,
    rgba(139, 92, 246, 0.03) 60%,
    transparent 100%
  );
  z-index: 2;
}

@keyframes gridPulse {
  0%, 100% { opacity: 0.3; }
  50% { opacity: 0.1; }
}

/* Simple Hero Icon */
.hero-icon-simple {
  margin-bottom: 2rem;
}

.main-icon-container {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 120px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  animation: iconFloat 3s ease-in-out infinite;
}

.main-hero-icon {
  font-size: 3rem;
  color: white;
}

@keyframes iconFloat {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

/* Enhanced Floating Icons */
.floating-icons {
  animation: fadeInDown 1s ease-out;
  margin-bottom: 3rem;
  z-index: 10;
  position: relative;
}

.icon-orbit-enhanced {
  position: relative;
  width: 250px;
  height: 250px;
  margin: 0 auto;
}

/* Orbit Rings */
.orbit-ring {
  position: absolute;
  border: 1px solid rgba(16, 185, 129, 0.2);
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: rotateRing 20s linear infinite;
}

.ring-1 {
  width: 200px;
  height: 200px;
  border-color: rgba(16, 185, 129, 0.3);
  animation-duration: 25s;
}

.ring-2 {
  width: 160px;
  height: 160px;
  border-color: rgba(59, 130, 246, 0.3);
  animation-duration: 20s;
  animation-direction: reverse;
}

.ring-3 {
  width: 120px;
  height: 120px;
  border-color: rgba(139, 92, 246, 0.3);
  animation-duration: 15s;
}

@keyframes rotateRing {
  from { transform: translate(-50%, -50%) rotate(0deg); }
  to { transform: translate(-50%, -50%) rotate(360deg); }
}

/* Central Core */
.orbit-core {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  background: radial-gradient(circle, rgba(16, 185, 129, 0.8), rgba(59, 130, 246, 0.6));
  border-radius: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 
    0 0 20px rgba(16, 185, 129, 0.4),
    0 0 40px rgba(16, 185, 129, 0.2);
  animation: coreGlow 3s ease-in-out infinite alternate;
}

@keyframes coreGlow {
  from { 
    box-shadow: 
      0 0 20px rgba(16, 185, 129, 0.4),
      0 0 40px rgba(16, 185, 129, 0.2);
  }
  to { 
    box-shadow: 
      0 0 30px rgba(16, 185, 129, 0.6),
      0 0 60px rgba(16, 185, 129, 0.3);
  }
}

.icon-orbit {
  position: relative;
  width: 200px;
  height: 200px;
  margin: 0 auto;
}

.orbit-icon {
  position: absolute;
  font-size: 2.5rem;
  border-radius: 50%;
  padding: 15px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  animation: orbitRotate 15s linear infinite;
  transition: all 0.3s ease;
}

/* Enhanced Pulse Glow Effect */
.pulse-glow {
  animation: orbitRotate 15s linear infinite, pulseGlow 3s ease-in-out infinite;
}

@keyframes pulseGlow {
  0%, 100% {
    box-shadow: 
      0 0 20px rgba(16, 185, 129, 0.3),
      inset 0 0 20px rgba(255, 255, 255, 0.1);
    transform: scale(1);
  }
  50% {
    box-shadow: 
      0 0 40px rgba(16, 185, 129, 0.5),
      0 0 60px rgba(59, 130, 246, 0.3),
      inset 0 0 20px rgba(255, 255, 255, 0.2);
    transform: scale(1.1);
  }
}

.icon-1 {
  color: #fbbf24;
  background: rgba(251, 191, 36, 0.15);
  top: 5%;
  left: 50%;
  transform: translateX(-50%);
  animation-delay: 0s;
  animation-duration: 20s;
  box-shadow: 0 0 30px rgba(251, 191, 36, 0.4);
}

.icon-2 {
  color: #3b82f6;
  background: rgba(59, 130, 246, 0.15);
  top: 25%;
  right: 10%;
  animation-delay: 3s;
  animation-duration: 18s;
  animation-direction: reverse;
  box-shadow: 0 0 30px rgba(59, 130, 246, 0.4);
}

.icon-3 {
  color: #10b981;
  background: rgba(16, 185, 129, 0.15);
  top: 70%;
  left: 10%;
  animation-delay: 6s;
  animation-duration: 22s;
  box-shadow: 0 0 30px rgba(16, 185, 129, 0.4);
}

.icon-4 {
  color: #8b5cf6;
  background: rgba(139, 92, 246, 0.15);
  bottom: 25%;
  right: 15%;
  animation-delay: 9s;
  animation-duration: 16s;
  animation-direction: reverse;
  box-shadow: 0 0 30px rgba(139, 92, 246, 0.4);
}

.icon-5 {
  color: #f59e0b;
  background: rgba(245, 158, 11, 0.15);
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  animation-delay: 12s;
  animation-duration: 24s;
  box-shadow: 0 0 30px rgba(245, 158, 11, 0.4);
}

.icon-6 {
  color: #ef4444;
  background: rgba(239, 68, 68, 0.15);
  top: 50%;
  left: 85%;
  animation-delay: 15s;
  animation-duration: 14s;
  animation-direction: reverse;
  box-shadow: 0 0 30px rgba(239, 68, 68, 0.4);
}

@keyframes orbitRotate {
  0% { transform: translateX(-50%) rotate(0deg); }
  100% { transform: translateX(-50%) rotate(360deg); }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Simple Hero Title */
.simple-hero-title {
  font-size: clamp(2.5rem, 6vw, 4rem);
  font-weight: 700;
  line-height: 1.2;
  text-align: center;
  letter-spacing: -0.01em;
  margin: 0;
  color: white;
  text-shadow: 0 2px 20px rgba(0, 0, 0, 0.3);
  animation: titleFadeIn 1s ease-out;
}

.simple-underline {
  width: 100px;
  height: 3px;
  background: linear-gradient(90deg, #fff, rgba(255, 255, 255, 0.5));
  margin: 1rem auto 0;
  border-radius: 2px;
  animation: underlineGrow 1s ease-out 0.5s both;
}

@keyframes titleFadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes underlineGrow {
  from { width: 0; }
  to { width: 100px; }
}

/* Enhanced Modern Title */
.modern-hero-title.enhanced {
  font-size: clamp(2.5rem, 8vw, 6rem);
  font-weight: 900;
  line-height: 0.9;
  text-align: center;
  letter-spacing: -0.02em;
  margin: 0;
  animation: titleReveal 2s ease-out;
}

.letter-animation {
  display: inline-block;
  background: linear-gradient(135deg, #ffffff 0%, #10b981 30%, #3b82f6 70%, #ffffff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: letterWave 4s ease-in-out infinite;
  text-shadow: 0 0 30px rgba(255, 255, 255, 0.3);
  transform-origin: center bottom;
}

@keyframes letterWave {
  0%, 100% {
    transform: translateY(0) rotateX(0) scale(1);
    filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.3));
  }
  25% {
    transform: translateY(-15px) rotateX(10deg) scale(1.05);
    filter: drop-shadow(0 0 30px rgba(16, 185, 129, 0.5));
  }
  50% {
    transform: translateY(0) rotateX(0) scale(1.1);
    filter: drop-shadow(0 0 40px rgba(59, 130, 246, 0.6));
  }
  75% {
    transform: translateY(8px) rotateX(-5deg) scale(1.05);
    filter: drop-shadow(0 0 30px rgba(139, 92, 246, 0.4));
  }
}

/* Letter Animation Delays */
.word-1 .letter-animation:nth-child(1) { animation-delay: 0s; }
.word-1 .letter-animation:nth-child(2) { animation-delay: 0.1s; }
.word-1 .letter-animation:nth-child(3) { animation-delay: 0.2s; }
.word-1 .letter-animation:nth-child(4) { animation-delay: 0.3s; }
.word-1 .letter-animation:nth-child(5) { animation-delay: 0.4s; }
.word-1 .letter-animation:nth-child(6) { animation-delay: 0.5s; }
.word-1 .letter-animation:nth-child(7) { animation-delay: 0.6s; }

.word-2 .letter-animation:nth-child(1) { animation-delay: 0.8s; }
.word-2 .letter-animation:nth-child(2) { animation-delay: 0.9s; }
.word-2 .letter-animation:nth-child(3) { animation-delay: 1s; }
.word-2 .letter-animation:nth-child(4) { animation-delay: 1.1s; }
.word-2 .letter-animation:nth-child(5) { animation-delay: 1.2s; }
.word-2 .letter-animation:nth-child(6) { animation-delay: 1.3s; }
.word-2 .letter-animation:nth-child(7) { animation-delay: 1.4s; }

.word-3 .letter-animation:nth-child(1) { animation-delay: 1.6s; }
.word-3 .letter-animation:nth-child(2) { animation-delay: 1.7s; }
.word-3 .letter-animation:nth-child(3) { animation-delay: 1.8s; }
.word-3 .letter-animation:nth-child(4) { animation-delay: 1.9s; }
.word-3 .letter-animation:nth-child(5) { animation-delay: 2s; }
.word-3 .letter-animation:nth-child(6) { animation-delay: 2.1s; }

.modern-hero-title {
  font-size: clamp(2.5rem, 8vw, 6rem);
  font-weight: 900;
  line-height: 0.9;
  text-align: center;
  letter-spacing: -0.02em;
  margin: 0;
  animation: titleReveal 2s ease-out;
}

.title-word {
  display: block;
  position: relative;
  background: linear-gradient(135deg, #ffffff, #e5e7eb);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: wordGlow 3s ease-in-out infinite;
}

.word-1 {
  animation-delay: 0.2s;
  background: linear-gradient(135deg, #fbbf24, #f59e0b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.word-2 {
  animation-delay: 0.4s;
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.word-3 {
  animation-delay: 0.6s;
  background: linear-gradient(135deg, #10b981, #059669);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.title-word::before {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  color: transparent;
  background: linear-gradient(135deg, #ffffff, #f3f4f6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  border-right: 2px solid #3b82f6;
  animation: typewriter 2s steps(20) infinite;
  overflow: hidden;
}

@keyframes titleReveal {
  0% {
    opacity: 0;
    transform: translateY(50px) scale(0.8);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes wordGlow {
  0%, 100% {
    filter: brightness(1) drop-shadow(0 0 20px rgba(255, 255, 255, 0.1));
  }
  50% {
    filter: brightness(1.2) drop-shadow(0 0 40px rgba(255, 255, 255, 0.2));
  }
}

@keyframes typewriter {
  0%, 50% { width: 0; }
  100% { width: 100%; border-right: 2px solid transparent; }
}

/* Enhanced Modern Underline */
.modern-underline-enhanced {
  position: relative;
  width: 300px;
  height: 6px;
  margin: 30px auto 0;
}

.underline-wave {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 3px;
  opacity: 0.8;
}

.wave-1 {
  background: linear-gradient(90deg, #10b981, #3b82f6);
  animation: waveMove 3s ease-in-out infinite, waveExpand 2s ease-out 1s forwards;
  transform-origin: left;
  transform: scaleX(0);
}

.wave-2 {
  background: linear-gradient(90deg, #3b82f6, #8b5cf6);
  animation: waveMove 3s ease-in-out infinite 0.5s, waveExpand 2s ease-out 1.2s forwards;
  transform-origin: right;
  transform: scaleX(0);
  opacity: 0.6;
}

.wave-3 {
  background: linear-gradient(90deg, #8b5cf6, #10b981);
  animation: waveMove 3s ease-in-out infinite 1s, waveExpand 2s ease-out 1.4s forwards;
  transform-origin: center;
  transform: scaleX(0);
  opacity: 0.4;
}

@keyframes waveExpand {
  to { transform: scaleX(1); }
}

@keyframes waveMove {
  0%, 100% {
    filter: brightness(1) saturate(1);
    box-shadow: 0 0 10px rgba(16, 185, 129, 0.3);
  }
  50% {
    filter: brightness(1.2) saturate(1.5);
    box-shadow: 0 0 20px rgba(59, 130, 246, 0.5);
  }
}

/* Modern Underline */
.modern-underline {
  width: 200px;
  height: 4px;
  background: linear-gradient(90deg, #fbbf24, #3b82f6, #10b981);
  background-size: 200% 200%;
  margin: 2rem auto 0;
  border-radius: 2px;
  animation: underlineFlow 3s ease-in-out infinite;
}

@keyframes underlineFlow {
  0%, 100% { 
    background-position: 0% 50%;
    transform: scaleX(1);
  }
  50% { 
    background-position: 100% 50%;
    transform: scaleX(1.2);
  }
}

/* Simple Tagline */
.simple-tagline {
  font-size: clamp(1rem, 2.5vw, 1.25rem);
  font-weight: 400;
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.9);
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
  animation: fadeInUp 1s ease-out 0.5s both;
}

/* Hero Tagline */
.hero-tagline {
  font-size: clamp(1.2rem, 3vw, 1.8rem);
  font-weight: 300;
  opacity: 0.9;
  line-height: 1.6;
  animation: fadeInUp 1s ease-out 0.8s both;
}

.tagline-highlight {
  background: linear-gradient(135deg, #fbbf24, #3b82f6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 600;
  animation: highlightPulse 2s ease-in-out infinite;
}

@keyframes highlightPulse {
  0%, 100% { opacity: 0.9; }
  50% { opacity: 1; }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Simple CTA Buttons */
.simple-cta-container {
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  animation: fadeInUp 1s ease-out 0.7s both;
}

.btn-primary-simple {
  background: rgba(255, 255, 255, 0.9) !important;
  color: #1e40af !important;
  border: none !important;
  font-weight: 600 !important;
  padding: 12px 32px !important;
  border-radius: 50px !important;
  transition: all 0.3s ease !important;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1) !important;
}

.btn-primary-simple:hover {
  background: white !important;
  color: #1e40af !important;
  transform: translateY(-2px) !important;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.2) !important;
}

.btn-secondary-simple {
  background: transparent !important;
  color: white !important;
  border: 2px solid rgba(255, 255, 255, 0.5) !important;
  font-weight: 500 !important;
  padding: 10px 30px !important;
  border-radius: 50px !important;
  transition: all 0.3s ease !important;
}

.btn-secondary-simple:hover {
  background: rgba(255, 255, 255, 0.1) !important;
  border-color: rgba(255, 255, 255, 0.8) !important;
  color: white !important;
  transform: translateY(-2px) !important;
}

/* Modern CTA Buttons */
.modern-cta-container {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
  animation: fadeInUp 1s ease-out 1s both;
}

.modern-btn {
  position: relative;
  padding: 1rem 2rem !important;
  border-radius: 50px !important;
  border: none !important;
  font-weight: 600 !important;
  font-size: 1.1rem !important;
  transition: all 0.3s ease !important;
  overflow: hidden !important;
  backdrop-filter: blur(10px) !important;
}

.modern-btn-primary {
  background: linear-gradient(135deg, #3b82f6, #1d4ed8) !important;
  color: white !important;
  box-shadow: 0 10px 30px rgba(59, 130, 246, 0.3) !important;
}

.modern-btn-secondary {
  background: rgba(255, 255, 255, 0.1) !important;
  color: white !important;
  border: 2px solid rgba(255, 255, 255, 0.2) !important;
  box-shadow: 0 10px 30px rgba(255, 255, 255, 0.1) !important;
}

.modern-btn:hover {
  transform: translateY(-5px) scale(1.05) !important;
}

.modern-btn-primary:hover {
  box-shadow: 0 20px 40px rgba(59, 130, 246, 0.4) !important;
}

.modern-btn-secondary:hover {
  background: rgba(255, 255, 255, 0.2) !important;
  border-color: rgba(255, 255, 255, 0.4) !important;
}

.btn-glow {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  transition: left 0.6s;
}

.modern-btn:hover .btn-glow {
  left: 100%;
}

/* Simple Features */
.simple-features {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  flex-wrap: wrap;
  margin-bottom: 2rem;
  animation: fadeInUp 1s ease-out 0.9s both;
}

.feature-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
  min-width: 80px;
}

.feature-item:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-3px);
}

.feature-icon {
  font-size: 1.5rem;
}

.feature-text {
  font-size: 0.875rem;
  font-weight: 500;
  color: white;
  text-align: center;
}

/* Trust Badges */
.trust-badges {
  display: flex;
  justify-content: center;
  gap: 2rem;
  flex-wrap: wrap;
  animation: fadeInUp 1s ease-out 1.2s both;
}

.badge {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.8rem 1.5rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 25px;
  backdrop-filter: blur(10px);
  font-size: 0.9rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.badge:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

.badge-icon {
  font-size: 1.2rem;
  color: #10b981;
  filter: drop-shadow(0 0 10px rgba(16, 185, 129, 0.3));
}

/* Simple Scroll Indicator */
.simple-scroll-indicator {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  animation: fadeInUp 1s ease-out 1.1s both;
  z-index: 30;
}

.scroll-arrow {
  font-size: 2rem;
  color: rgba(255, 255, 255, 0.7);
  animation: bounce 2s infinite;
  display: block;
  margin: 0 auto 0.5rem;
}

.scroll-text-simple {
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.6);
  margin: 0;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-8px);
  }
  60% {
    transform: translateY(-4px);
  }
}

/* Scroll Indicator */
.scroll-indicator {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  animation: fadeInUp 1s ease-out 1.5s both;
  z-index: 30;
  margin-bottom: 1rem;
}

.scroll-mouse {
  width: 24px;
  height: 40px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 12px;
  margin: 0 auto 0.5rem;
  position: relative;
}

.scroll-wheel {
  width: 3px;
  height: 8px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 2px;
  position: absolute;
  top: 8px;
  left: 50%;
  transform: translateX(-50%);
  animation: scrollWheel 2s ease-in-out infinite;
}

@keyframes scrollWheel {
  0%, 100% { 
    top: 8px;
    opacity: 1;
  }
  50% { 
    top: 20px;
    opacity: 0.3;
  }
}

.scroll-text {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.6);
  margin: 0;
}

/* Trust Indicators */
.trust-indicators {
  animation: fadeInUp 1s ease-out 1.2s both;
}

.trust-indicators .flex:hover {
  transform: translateY(-2px);
  transition: transform 0.3s ease;
}

/* Hero Visual Elements */
.hero-visual {
  animation: slideInRight 1s ease-out 0.3s both;
}

