.visual-card-container {
  position: relative;
}

.visual-card {
  position: relative;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.1);
  overflow: hidden;
}

.visual-card:hover {
  transform: translateY(-10px) scale(1.02);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
}

.visual-icons {
  display: flex;
  justify-content: center;
  gap: 2rem;
  position: relative;
}

.visual-icon {
  font-size: 3rem;
  animation: iconOrbit 8s linear infinite;
}

.primary-icon {
  color: #fbbf24;
  animation-delay: 0s;
  filter: drop-shadow(0 0 15px rgba(251, 191, 36, 0.6));
}

.secondary-icon {
  color: #60a5fa;
  animation-delay: 2.7s;
  filter: drop-shadow(0 0 15px rgba(96, 165, 250, 0.6));
}

.tertiary-icon {
  color: #34d399;
  animation-delay: 5.4s;
  filter: drop-shadow(0 0 15px rgba(52, 211, 153, 0.6));
}

@keyframes iconOrbit {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg);
  }
  33% { 
    transform: translateY(-10px) rotate(120deg);
  }
  66% { 
    transform: translateY(5px) rotate(240deg);
  }
}

.visual-title {
  background: linear-gradient(135deg, #ffffff, #e5e7eb);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: titleShimmer 3s ease-in-out infinite;
}

@keyframes titleShimmer {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.8; }
}

.visual-description {
  animation: fadeInUp 1s ease-out 1s both;
}

.visual-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 200px;
  height: 200px;
  background: radial-gradient(circle, rgba(96, 165, 250, 0.1) 0%, transparent 70%);
  border-radius: 50%;
  animation: visualGlow 4s ease-in-out infinite alternate;
  z-index: -1;
}

@keyframes visualGlow {
  0% { 
    opacity: 0.3; 
    transform: translate(-50%, -50%) scale(0.8);
  }
  100% { 
    opacity: 0.6; 
    transform: translate(-50%, -50%) scale(1.2);
  }
}

