import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/login',
    name: 'login',
    component: () => import(/* webpackChunkName: "login" */ '../views/LoginView.vue'),
    meta: { requiresGuest: true }
  },
  {
    path: '/register',
    name: 'register',
    component: () => import(/* webpackChunkName: "register" */ '../views/RegisterView.vue'),
    meta: { requiresGuest: true }
  },
  {
    path: '/verification',
    name: 'verification',
    component: () => import(/* webpackChunkName: "verification" */ '../views/VerificationView.vue'),
    meta: { requiresGuest: true }
  },
  {
    path: '/dashboard',
    component: () => import(/* webpackChunkName: "dashboard" */ '../views/DashboardView.vue'),
    meta: { requiresAuth: true },
    redirect: { name: 'dashboard-home' },
    children: [
      {
        path: '',
        name: 'dashboard-home',
        component: () => import(/* webpackChunkName: "dashboard-home" */ '../views/dashboard/DashboardHome.vue')
      },
      {
        path: 'my-reports',
        name: 'my-reports',
        component: () => import(/* webpackChunkName: "my-reports" */ '../views/dashboard/MyReports.vue')
      },
      {
        path: 'all-reports',
        name: 'all-reports',
        component: () => import(/* webpackChunkName: "all-reports" */ '../views/dashboard/AllReports.vue')
      },
      {
        path: 'pending-reports',
        name: 'pending-reports',
        component: () => import(/* webpackChunkName: "pending-reports" */ '../views/dashboard/PendingReports.vue')
      },
      {
        path: 'create-report',
        name: 'create-report',
        component: () => import(/* webpackChunkName: "create-report" */ '../views/dashboard/CreateReport.vue')
      },
      {
        path: 'analytics',
        name: 'analytics',
        component: () => import(/* webpackChunkName: "analytics" */ '../views/dashboard/Analytics.vue')
      },
      {
        path: 'manage-users',
        name: 'manage-users',
        component: () => import(/* webpackChunkName: "manage-users" */ '../views/dashboard/ManageUsers.vue')
      },
      {
        path: 'evidence',
        name: 'evidence',
        component: () => import(/* webpackChunkName: "evidence" */ '../views/dashboard/Evidence.vue')
      },
      {
        path: 'manage-evidence',
        name: 'manage-evidence',
        component: () => import(/* webpackChunkName: "manage-evidence" */ '../views/dashboard/ManageEvidence.vue')
      },
      {
        path: 'user-permissions',
        name: 'user-permissions',
        component: () => import(/* webpackChunkName: "user-permissions" */ '../views/dashboard/UserPermissions.vue')
      },
      {
        path: 'user-activity',
        name: 'user-activity',
        component: () => import(/* webpackChunkName: "user-activity" */ '../views/dashboard/UserActivity.vue')
      },
      {
        path: 'notifications',
        name: 'notifications',
        component: () => import(/* webpackChunkName: "notifications" */ '../views/dashboard/Notifications.vue')
      },
      {
        path: 'profile',
        name: 'profile',
        component: () => import(/* webpackChunkName: "profile" */ '../views/dashboard/Profile.vue')
      },
      {
        path: 'settings',
        name: 'settings',
        component: () => import(/* webpackChunkName: "settings" */ '../views/dashboard/Settings.vue')
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

// Navigation guards
router.beforeEach((to, from, next) => {
  const isLoggedIn = store.getters.isLoggedIn;
  
  console.log('Navigation Guard:', {
    to: to.path,
    from: from.path,
    isLoggedIn,
    token: store.state.token
  });
  
  // Check if route requires authentication
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!isLoggedIn) {
      console.log('Redirecting to login - not authenticated');
      next({ name: 'login' });
    } else {
      console.log('Allowing access to protected route');
      next();
    }
  }
  // Check if route requires guest (not logged in)
  else if (to.matched.some(record => record.meta.requiresGuest)) {
    if (isLoggedIn) {
      console.log('Redirecting to dashboard - already authenticated');
      next({ name: 'dashboard-home' });
    } else {
      console.log('Allowing access to guest route');
      next();
    }
  }
  // No restrictions
  else {
    console.log('No restrictions - allowing access');
    next();
  }
});

export default router
